// Element UI 


// 
import TimeLine from "./components/timeLine.vue";

// 
const components = [
    TimeLine
]

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  components.map(component => Vue.component(component.name, component))
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  // 
  ...components
}

